*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
  font-weight: bold;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.btn {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #134cca;
  cursor: pointer;
}

.acciones {
  display: flex;
  justify-content: space-between;
}
.acciones a {
  color: #000;
  margin-top: 2rem;
  font-weight: bold;
}

.alertas {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 0rem auto 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alertas.error {
  background-color: #ffa9a9;
  border-left: 0.7rem solid rgb(169, 0, 0);
}
.alertas.exito {
  background-color: #78ff78;
  border-left: 0.7rem solid rgb(0, 120, 0);
}

.login .titulo {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 10rem;
}
.login .btn {
  background-color: #0891B2;
}
.login .acciones a {
  color: #0891B2;
}

.crear .titulo {
  background: linear-gradient(to right, #7C3AED 0%, #d9c6fa 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 10rem;
}
.crear .btn {
  background-color: #7C3AED;
}
.crear .acciones a {
  color: #7C3AED;
}

.olvide .titulo {
  background: linear-gradient(to right, #2563EB 0%, #b1c7f8 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 10rem;
}
.olvide .btn {
  background-color: #2563EB;
}
.olvide .acciones a {
  color: #2563EB;
}

.recuperar .titulo {
  background: linear-gradient(to right, rgb(169, 0, 0) 0%, #ff4343 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 10rem;
}
.recuperar .btn {
  background-color: rgb(169, 0, 0);
}
.recuperar .acciones a {
  color: rgb(169, 0, 0);
}

.mensaje .titulo {
  background: linear-gradient(to right, #F59E0B 0%, #fbd89e 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 10rem;
}

.confirmar .titulo {
  background: linear-gradient(to right, #4338CA 0%, #b5b1ea 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 10rem;
}
.confirmar .acciones {
  justify-content: center;
}
.confirmar .acciones a {
  color: #4338CA;
}

.dashboard {
  background-color: #bac0ca;
  display: flex;
  height: 100vh;
}
.dashboard .principal {
  flex: 1;
  overflow-y: scroll;
}
.dashboard .principal .contenido {
  padding: 2rem;
}
.dashboard .principal .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}

.sidebar {
  background-color: #4338CA;
  padding: 2rem;
}
.sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 1.5rem;
  padding: 0.7rem;
  transition: background-color 500ms ease;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #28217b;
  border-radius: 0.5rem;
}

.barra {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra p {
  margin: 0;
  font-size: 2rem;
}
.barra p span {
  font-weight: 700;
  color: #4338CA;
}
.barra .salir {
  background-color: #352ca3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .barra .salir {
    width: auto;
  }
}
.barra .salir:hover {
  background-color: #28217b;
  cursor: pointer;
}

.no-proyectos {
  color: #FFFFFF;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.listado-proyectos .proyecto {
  background-color: #564ccf;
  padding: 1rem;
  margin-top: 1rem;
  display: flex;
  align-items: center;
  min-height: 10rem;
  line-height: 2;
  border-radius: 0.5rem;
  transition: background-color 400ms ease;
}
.listado-proyectos .proyecto:hover {
  background-color: #3b31b8;
}
.listado-proyectos .proyecto a {
  color: #FFFFFF;
  font-weight: 900;
}

.contenedor-nueva-tarea .agregar-tarea {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #066981;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  margin-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}
.listado-tareas .tarea {
  padding: 0.5rem 2rem;
  border-bottom: 0.2rem solid #808b9e;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.listado-tareas .tarea:last-of-type {
  border: none;
}
.listado-tareas .tarea p {
  font-size: 1.7rem;
}
.listado-tareas .tarea .opciones {
  display: flex;
  gap: 1.5rem;
}
.listado-tareas .tarea .opciones button {
  border: none;
  font-size: 1rem;
  padding: 0.8rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
.listado-tareas .tarea .opciones button:hover {
  cursor: pointer;
}
.listado-tareas .tarea .opciones button.completo {
  background-color: #2563EB;
  color: #FFFFFF;
}
.listado-tareas .tarea .opciones button.pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.listado-tareas .tarea .opciones button.eliminar-tarea {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transition: all 500ms ease-in-out;
  transform: translateY(-30rem);
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .opciones .submit-nueva-tarea {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .opciones .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .opciones .submit-nueva-tarea:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .formulario .opciones .cerrar-modal {
  background-color: rgb(169, 0, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .opciones .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .opciones .cerrar-modal:hover {
  background-color: #760000;
  cursor: pointer;
}

.filtros {
  background-color: #FFFFFF;
  margin-top: 2rem;
  border-radius: 1rem;
  padding: 2rem;
}
.filtros .filtros-inputs {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.filtros .filtros-inputs h2 {
  font-size: 3rem;
  color: #4338CA;
  margin: 0;
}
.filtros .filtros-inputs .campo {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.filtros .filtros-inputs .campo input[type=radio] {
  accent-color: #4338CA;
  width: 2rem;
  height: 2rem;
}